'use client';

import { useState, useEffect } from 'react';
import { useRequireAuth, useAuth } from '@/contexts/AuthContext';
import { accountService } from '@/lib/firebase-service';
import { Account } from '@/lib/types';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Badge } from '@/components/ui/badge';
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs';
import { Alert, AlertDescription } from '@/components/ui/alert';

export default function AdminDashboard() {
  const { user, loading, authorized } = useRequireAuth(['Admin']);
  const [municipals, setMunicipals] = useState<Account[]>([]);
  const [establishments, setEstablishments] = useState<Account[]>([]);
  const [drivers, setDrivers] = useState<Account[]>([]);
  const [loadingData, setLoadingData] = useState(true);
  const [error, setError] = useState('');

  useEffect(() => {
    if (authorized) {
      loadDashboardData();
    }
  }, [authorized]);

  const loadDashboardData = async () => {
    try {
      setLoadingData(true);
      
      // Load all account types
      const [municipalResponse, establishmentResponse, driverResponse] = await Promise.all([
        accountService.getByType('Municipal'),
        accountService.getByType('Establishment'),
        accountService.getByType('Driver')
      ]);

      if (municipalResponse.success && municipalResponse.data) {
        setMunicipals(municipalResponse.data);
      }
      
      if (establishmentResponse.success && establishmentResponse.data) {
        setEstablishments(establishmentResponse.data);
      }
      
      if (driverResponse.success && driverResponse.data) {
        setDrivers(driverResponse.data);
      }
    } catch (err) {
      setError('Failed to load dashboard data');
      console.error('Error loading dashboard data:', err);
    } finally {
      setLoadingData(false);
    }
  };

  const { signOut } = useAuth();

  const handleSignOut = async () => {
    try {
      await signOut();
      window.location.href = '/login';
    } catch (error) {
      console.error('Error signing out:', error);
    }
  };

  if (loading || !authorized) {
    return (
      <div className="min-h-screen flex items-center justify-center bg-background">
        <div className="text-center">
          <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-primary mx-auto mb-4"></div>
          <h2 className="text-xl font-semibold">Loading Admin Dashboard...</h2>
        </div>
      </div>
    );
  }

  const totalRevenue = municipals.reduce((sum, m) => sum + (m.monthlyIncome || 0), 0) + 
                      establishments.reduce((sum, e) => sum + (e.monthlyIncome || 0), 0);
  
  const totalParkingSlots = municipals.reduce((sum, m) => sum + (m.totalParkingSlots || 0), 0) + 
                           establishments.reduce((sum, e) => sum + (e.totalParkingSlots || 0), 0);

  const dashboardImage = "https://storage.googleapis.com/workspace-0f70711f-8b4e-4d94-86f1-2a93ccde5887/image/75ebe796-231d-427f-a9db-6a62e76eb889.png";

  return (
    <div className="min-h-screen bg-background">
      {/* Header */}
      <header className="border-b bg-card">
        <div className="container mx-auto px-4 py-4 flex justify-between items-center">
          <div>
            <h1 className="text-2xl font-bold text-foreground">ParkShare Admin</h1>
            <p className="text-muted-foreground">Master Dashboard</p>
          </div>
          <div className="flex items-center space-x-4">
            <span className="text-sm text-muted-foreground">Welcome, {user?.displayName}</span>
            <Button variant="outline" onClick={handleSignOut}>
              Sign Out
            </Button>
          </div>
        </div>
      </header>

      <div className="container mx-auto px-4 py-8">
        {error && (
          <Alert variant="destructive" className="mb-6">
            <AlertDescription>{error}</AlertDescription>
          </Alert>
        )}

        {/* Hero Section */}
        <div className="mb-8">
          <img 
            src={dashboardImage}
            alt="ParkShare admin dashboard with comprehensive analytics and client management interface"
            className="w-full h-48 object-cover rounded-lg mb-4"
            onError={(e) => {
              e.currentTarget.style.display = 'none';
            }}
          />
        </div>

        {/* Overview Stats */}
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6 mb-8">
          <Card>
            <CardHeader className="pb-2">
              <CardTitle className="text-sm font-medium text-muted-foreground">Total Clients</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="text-2xl font-bold">{municipals.length + establishments.length}</div>
              <p className="text-xs text-muted-foreground">
                {municipals.length} Municipals, {establishments.length} Establishments
              </p>
            </CardContent>
          </Card>

          <Card>
            <CardHeader className="pb-2">
              <CardTitle className="text-sm font-medium text-muted-foreground">Total Drivers</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="text-2xl font-bold">{drivers.length}</div>
              <p className="text-xs text-muted-foreground">
                {drivers.filter(d => d.isActive).length} Active
              </p>
            </CardContent>
          </Card>

          <Card>
            <CardHeader className="pb-2">
              <CardTitle className="text-sm font-medium text-muted-foreground">Total Parking Slots</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="text-2xl font-bold">{totalParkingSlots}</div>
              <p className="text-xs text-muted-foreground">Across all clients</p>
            </CardContent>
          </Card>

          <Card>
            <CardHeader className="pb-2">
              <CardTitle className="text-sm font-medium text-muted-foreground">Monthly Revenue</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="text-2xl font-bold">₱{totalRevenue.toLocaleString()}</div>
              <p className="text-xs text-muted-foreground">System-wide</p>
            </CardContent>
          </Card>
        </div>

        {/* Client Management Tabs */}
        <Tabs defaultValue="municipals" className="space-y-6">
          <TabsList className="grid w-full grid-cols-3">
            <TabsTrigger value="municipals">Municipals ({municipals.length})</TabsTrigger>
            <TabsTrigger value="establishments">Establishments ({establishments.length})</TabsTrigger>
            <TabsTrigger value="drivers">Drivers ({drivers.length})</TabsTrigger>
          </TabsList>

          <TabsContent value="municipals" className="space-y-4">
            <div className="flex justify-between items-center">
              <h2 className="text-xl font-semibold">Municipal Clients</h2>
              <Button>Add New Municipal</Button>
            </div>
            
            {loadingData ? (
              <div className="text-center py-8">
                <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-primary mx-auto mb-4"></div>
                <p>Loading municipals...</p>
              </div>
            ) : (
              <div className="grid gap-4">
                {municipals.map((municipal) => (
                  <Card key={municipal.id}>
                    <CardHeader>
                      <div className="flex justify-between items-start">
                        <div>
                          <CardTitle className="text-lg">{municipal.businessName || 'Municipal Account'}</CardTitle>
                          <CardDescription>{municipal.address}</CardDescription>
                        </div>
                        <Badge variant={municipal.isActive ? "default" : "secondary"}>
                          {municipal.isActive ? 'Active' : 'Inactive'}
                        </Badge>
                      </div>
                    </CardHeader>
                    <CardContent>
                      <div className="grid grid-cols-2 md:grid-cols-4 gap-4 text-sm">
                        <div>
                          <p className="text-muted-foreground">Contact</p>
                          <p className="font-medium">{municipal.contactPerson}</p>
                        </div>
                        <div>
                          <p className="text-muted-foreground">Parking Slots</p>
                          <p className="font-medium">{municipal.totalParkingSlots || 0}</p>
                        </div>
                        <div>
                          <p className="text-muted-foreground">Monthly Revenue</p>
                          <p className="font-medium">₱{(municipal.monthlyIncome || 0).toLocaleString()}</p>
                        </div>
                        <div>
                          <p className="text-muted-foreground">Joined</p>
                          <p className="font-medium">{municipal.createdAt?.toLocaleDateString()}</p>
                        </div>
                      </div>
                      <div className="flex space-x-2 mt-4">
                        <Button variant="outline" size="sm">View Details</Button>
                        <Button variant="outline" size="sm">Manage</Button>
                      </div>
                    </CardContent>
                  </Card>
                ))}
                
                {municipals.length === 0 && (
                  <Card>
                    <CardContent className="text-center py-8">
                      <p className="text-muted-foreground">No municipal clients found</p>
                    </CardContent>
                  </Card>
                )}
              </div>
            )}
          </TabsContent>

          <TabsContent value="establishments" className="space-y-4">
            <div className="flex justify-between items-center">
              <h2 className="text-xl font-semibold">Establishment Clients</h2>
              <Button>Add New Establishment</Button>
            </div>
            
            {loadingData ? (
              <div className="text-center py-8">
                <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-primary mx-auto mb-4"></div>
                <p>Loading establishments...</p>
              </div>
            ) : (
              <div className="grid gap-4">
                {establishments.map((establishment) => (
                  <Card key={establishment.id}>
                    <CardHeader>
                      <div className="flex justify-between items-start">
                        <div>
                          <CardTitle className="text-lg">{establishment.businessName}</CardTitle>
                          <CardDescription>{establishment.address}</CardDescription>
                        </div>
                        <Badge variant={establishment.isActive ? "default" : "secondary"}>
                          {establishment.isActive ? 'Active' : 'Inactive'}
                        </Badge>
                      </div>
                    </CardHeader>
                    <CardContent>
                      <div className="grid grid-cols-2 md:grid-cols-4 gap-4 text-sm">
                        <div>
                          <p className="text-muted-foreground">Contact</p>
                          <p className="font-medium">{establishment.contactPerson}</p>
                        </div>
                        <div>
                          <p className="text-muted-foreground">Parking Slots</p>
                          <p className="font-medium">{establishment.totalParkingSlots || 0}</p>
                        </div>
                        <div>
                          <p className="text-muted-foreground">Monthly Revenue</p>
                          <p className="font-medium">₱{(establishment.monthlyIncome || 0).toLocaleString()}</p>
                        </div>
                        <div>
                          <p className="text-muted-foreground">Joined</p>
                          <p className="font-medium">{establishment.createdAt?.toLocaleDateString()}</p>
                        </div>
                      </div>
                      <div className="flex space-x-2 mt-4">
                        <Button variant="outline" size="sm">View Details</Button>
                        <Button variant="outline" size="sm">Manage</Button>
                      </div>
                    </CardContent>
                  </Card>
                ))}
                
                {establishments.length === 0 && (
                  <Card>
                    <CardContent className="text-center py-8">
                      <p className="text-muted-foreground">No establishment clients found</p>
                    </CardContent>
                  </Card>
                )}
              </div>
            )}
          </TabsContent>

          <TabsContent value="drivers" className="space-y-4">
            <div className="flex justify-between items-center">
              <h2 className="text-xl font-semibold">Driver Users</h2>
              <Button>Export Driver Data</Button>
            </div>
            
            {loadingData ? (
              <div className="text-center py-8">
                <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-primary mx-auto mb-4"></div>
                <p>Loading drivers...</p>
              </div>
            ) : (
              <div className="grid gap-4">
                {drivers.slice(0, 10).map((driver) => (
                  <Card key={driver.id}>
                    <CardHeader>
                      <div className="flex justify-between items-start">
                        <div>
                          <CardTitle className="text-lg">
                            {driver.firstName} {driver.lastName}
                          </CardTitle>
                          <CardDescription>{driver.email}</CardDescription>
                        </div>
                        <div className="flex space-x-2">
                          <Badge variant={driver.licenseVerified ? "default" : "secondary"}>
                            {driver.licenseVerified ? 'Verified' : 'Pending'}
                          </Badge>
                          <Badge variant={driver.isActive ? "default" : "secondary"}>
                            {driver.isActive ? 'Active' : 'Inactive'}
                          </Badge>
                        </div>
                      </div>
                    </CardHeader>
                    <CardContent>
                      <div className="grid grid-cols-2 md:grid-cols-4 gap-4 text-sm">
                        <div>
                          <p className="text-muted-foreground">License ID</p>
                          <p className="font-medium">{driver.licenseId || 'Not provided'}</p>
                        </div>
                        <div>
                          <p className="text-muted-foreground">Balance</p>
                          <p className="font-medium">₱{(driver.availableBalance || 0).toLocaleString()}</p>
                        </div>
                        <div>
                          <p className="text-muted-foreground">Mobile</p>
                          <p className="font-medium">{driver.mobileNumber || 'Not provided'}</p>
                        </div>
                        <div>
                          <p className="text-muted-foreground">Joined</p>
                          <p className="font-medium">{driver.createdAt?.toLocaleDateString()}</p>
                        </div>
                      </div>
                      <div className="flex space-x-2 mt-4">
                        <Button variant="outline" size="sm">View Profile</Button>
                        {!driver.licenseVerified && (
                          <Button variant="outline" size="sm">Verify License</Button>
                        )}
                      </div>
                    </CardContent>
                  </Card>
                ))}
                
                {drivers.length === 0 && (
                  <Card>
                    <CardContent className="text-center py-8">
                      <p className="text-muted-foreground">No drivers found</p>
                    </CardContent>
                  </Card>
                )}
                
                {drivers.length > 10 && (
                  <Card>
                    <CardContent className="text-center py-4">
                      <p className="text-muted-foreground">
                        Showing 10 of {drivers.length} drivers
                      </p>
                      <Button variant="outline" className="mt-2">View All Drivers</Button>
                    </CardContent>
                  </Card>
                )}
              </div>
            )}
          </TabsContent>
        </Tabs>
      </div>
    </div>
  );
}
